import { cilPlus, cilX} from '@coreui/icons'
import CIcon from '@coreui/icons-react'
import {  
  CButton,
  CCard,
  CCardBody,
  CCol,
  CForm,
  CFormInput,
  CFormSelect,
  CFormTextarea,
  CRow
} from '@coreui/react'
import React, { useEffect, useState } from 'react'
import { Link, generatePath, useNavigate } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify'
import  './cms.scss';

import { BiBookContent, BiImageAlt, BiListCheck, BiParagraph } from "react-icons/bi";
import { PiTextTBold } from "react-icons/pi";
import { BsLink45Deg } from 'react-icons/bs'




const AddPages = () => { 
  const Navigate = useNavigate()
  const [validated, setValidated] = useState(false)
  const [updateList, setUpdateList] = useState({});
  const [activeSect, setActiveSect] = useState(0);
  const [selectedSect, setSelectedSect]  = useState("");
  
  const [values, setValues]  = useState([]);

  const [mainArr, setMainArr] = useState([]);


  const handleSubmit = (e) => {
    e.preventDefault();
    const form = e.currentTarget;
    const [name, description, meta_title, meta_description, status] = e.target

    if (form.checkValidity() === false) {
      setValidated(true)
      e.stopPropagation()
    } else {
      setValidated(false)

      var requestOptions = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          page_name: name.value,
          page_description: description.value || '',
          meta_title: meta_title.value || '',
          meta_description: meta_description.value || '',
          status: status.value || 0,
        }),
      }

      const url = process.env.REACT_APP_API_URL + 'pages/create'

      fetch(url, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          if (result.status == 200) {
            toast.success(result.message)
            setUpdateList(result.data)
            setValidated(false)

            e.target.reset()
          } else {
            toast.error(result.message)
          }
        })
        .catch((error) => console.log('error', error))
    }
  }

  const handleSection = (e)=>{
    setSelectedSect(e);
    setActiveSect(2)
  } 

  const handleAdd = ()=> setActiveSect(1);

  const fieldTypes = [
    {
      name:"Short Text",
      icon:<PiTextTBold/>,
      type:"text"
    },
    {
      name:"Short Text",
      icon:<BiParagraph/> ,
      type:"textarea"
    },
    {
      name:"Image",
      icon:<BiImageAlt/>,
      type:"img"
    },
    {
      name:"Link",
      icon:<BsLink45Deg/>,
      type:"url"
    }
  ]
  const generateKey = (arg)=>arg+"_"+Math.floor(Math.random()*99999);
  const handleField =(type, id)=>{
    const outPut = "";
    setMainArr((prev)=>{     
      console.log(prev);
      setActiveSect(0)
      return [...prev, {section_prop:"Section "+prev.length, section_value:[{type, id}]}]
    })
  }

  return (
    <>
      <ToastContainer />
      <h1 className="headingType1 text-center mb-4">Pages</h1>
      <CForm
        className="g-3 needs-validation"
        noValidate
        validated={validated}
        onSubmit={handleSubmit}
      >
        <CRow className="g-5 justify-content-center">
          <CCol lg={8}>
            <CCard>
              <CCardBody className="p-5">
                <div className="mb-4">
                  <CFormInput
                    type="text"
                    defaultValue=""
                    feedbackInvalid="Please enter a Brand Name."
                    id="validationCustom1"
                    label="Page Name"
                    name="name"
                    required
                  />
                </div>
                <div className="mb-4">
                  <CFormTextarea
                    defaultValue=""
                    feedbackInvalid="Please Select Status."
                    label="Description"
                    id="validationCustom2"
                    name="description"
                    rows={6}
                  />
                </div>
              </CCardBody>
            </CCard>
          </CCol>
          <CCol lg={4}>
            <CCard>
              <CCardBody className="p-5">
                <div className="mb-4">
                  <CFormInput
                    type="text"
                    defaultValue=""
                    feedbackInvalid="Please enter a Brand Name."
                    id="validationCustom3"
                    label="Meta Title"
                    name="meta_title"
                  />
                </div>
                <div className="mb-4">
                  <CFormTextarea
                    defaultValue=""
                    feedbackInvalid="Please Select Status."
                    label="Meta Description"
                    id="validationCustom3"
                    name="meta_description"
                  />
                </div>
                <div className="mb-4">
                  <CFormSelect
                    type="text"
                    defaultValue=""
                    feedbackInvalid="Please Select Status."
                    id="validationCustom4"
                    label="Status"
                    name="status"
                    required
                  >
                    <option value={'1'}>Active</option>
                    <option value={'0'}>Inactive</option>
                  </CFormSelect>
                </div>
                <div className="text-center mt-4">
                  <CButton color="primary" type="submit" className="py-2 px-5">
                    Add Page
                  </CButton>
                </div>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CForm>

      <CCard  className='mt-4'>
        <CCardBody className="p-5">
          <h4 className='mb-4'>Add Sections</h4>

          {mainArr.map((item, key)=>(
            <div className='each_sections' key={key}>
              <div className='each_section_header'>
                <CFormInput defaultValue={item.section_prop} placeholder='Section Title...'/>
              </div>
              <div className='each_section_body'>
              {item.section_value && item.section_value.map((subItem, subKey)=>(                
                <div className='each_single_items' key={subKey}>  
                  {subItem.type==="text"&&<CFormInput label="Small Text.." className="" defaultValue={subItem.value}/>}
                  {subItem.type==="img"&&<CFormInput label="Upload Image.." type='file' className="" defaultValue={subItem.value}/>}
                  {subItem.type==="textarea"&&<CFormTextarea  label="Enter Long Text.." className="" defaultValue={subItem.value}/>} 
                  {subItem.type==="url"&&<CRow>
                    <CCol>
                      <CFormInput label="Link Text" className="" defaultValue={subItem.value}/>
                    </CCol>
                    <CCol>
                      <CFormInput label="Link Value" className="" defaultValue={subItem.value}/>
                    </CCol>
                  </CRow>}
                  <div className='text-ceter'>
                    <CButton color='primary'>Add Item</CButton>
                  </div>
                </div> 
              ))}   
              </div>
            </div>
          ))}

          {activeSect==2&&selectedSect=="single"&& 
            <div className='single_object_selected mb-5'>
                <CRow>
                  {fieldTypes.map((i, key)=>(
                    <CCol lg={3} key={key}>
                      <div  className='each_section_Wrp' onClick={(e)=>handleField(i.type, generateKey(i.type))} data-id={generateKey(i.type)}>
                        <div className='aicon'>
                           {i.icon}           
                        </div>
                        <div className='section_title'>
                          {i.name}
                        </div>
                      </div>
                    </CCol>
                  ))} 
                </CRow>
            </div>
          }
          {activeSect==2&&selectedSect=="repeater"&& 
            <div className='repeater_object_selected mb-5'>
                <CRow>                 
                  {fieldTypes.map((i, key)=>(
                    <CCol lg={3} key={key} data-id={generateKey(i.type)}>
                      <div  className='each_section_Wrp' >
                        <div className='aicon'>
                           {i.icon}           
                        </div>
                        <div className='section_title'>
                          {i.name}
                        </div>
                      </div>
                    </CCol>
                  ))}
                </CRow>
            </div>
          }
          {activeSect==1 &&
          <div className='allSectionTypeWrp'>
            <div className='removeBtn'>
              <CIcon icon={cilX} customClassName="nav-icon" />
            </div>
            <CRow className='g-3'>              
              <CCol lg="3">
                <div  className='each_section_Wrp' onClick={()=>handleSection("single")}>
                  <div className='aicon'>
                    <BiBookContent/>               
                  </div>
                  <div className='section_title'>
                    Single 
                  </div>
                </div>
              </CCol>
              <CCol lg="3">
                <div className='each_section_Wrp' onClick={()=>handleSection("Repeater")}> 
                  <div className='aicon'>
                    <BiListCheck/>               
                  </div>
                  <div className='section_title'>
                    Repeater
                  </div>
                </div>
              </CCol>
            </CRow>
          </div>}
          {activeSect==0&&
          <div className='addNewSection' onClick={handleAdd}>
            <CIcon icon={cilPlus} customClassName="nav-icon" />
          </div>}
        </CCardBody>
      </CCard>
    </>
  )
}

export default AddPages
